.hero{
    display:flex;
    width: 90%;
    margin:0 auto;
    height:100vh;
}
.hero::before{
    content:"HELLO";
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    font-size:19em;
    display: flex;
    align-items: center;
    font-weight: 900;
    z-index: -1;
    color:#BDC0BA;
    opacity: 0.3;
    font-family: 'Noto Sans TC', sans-serif;
    letter-spacing: 3px;
}
.hero::after{
    content:"WORLD";
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    font-size:19em;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-weight: 900;
    z-index: -1;
    color:#BDC0BA;
    opacity: 0.3;
    font-family: 'Noto Sans TC', sans-serif;
    letter-spacing: 3px;
}
/*hero_text {h1, h2, p}*/
.hero_text{
    flex:1;
    display:flex;
    flex-direction: column;
    margin-left: 90px;
    z-index: 1;
}
.hero_text h1{
    font-size:6em;
    line-height: 120px;
    text-shadow: 1px 1px 3px #BDC0BA;
    color:#052f57;
    font-weight: 700;
}
.hero_text h2{
    font-size: 2em;
    text-shadow: 1px 1px 3px #BDC0BA;
    margin-left:7px;
}
.hero_text p{
    width: 97%;
    font-size:1.2em;
    font-weight: 400;
    padding-bottom:10px;
    line-height: 30px;
    margin-left:9px;
}
.hero_text :nth-child(5){
    font-weight: 400;
}
.hero button{
    margin-left:10px
}
.column :nth-child(1){
    flex: 45%;
    font-weight: 500;
}
.column :nth-child(2){
    flex: 55%;
}
/*hero_img {h1, h2, p}*/
.hero_img{
    flex:1;
    display: flex;
    justify-content:center;
    align-items: center;
    z-index: 1;
}

.hero_img svg{
    width:100%;
}

@media (max-width: 1024px){ 
    /*hero_text {h1, h2, p} mobile*/
    .hero{
        width: 100%;
        margin:0 auto;
        flex-direction: column;
        height:auto;
        flex-direction: column-reverse;
    }
    .hero::before{
        content:"HELLO";
        position: absolute;
        top:0;
        left:0;
        font-size:100px;
        font-weight: 900;
        z-index: 0;
        color:#BDC0BA;
        opacity: 0.3;
        font-family: 'Noto Sans TC', sans-serif;
        letter-spacing: 3px;
        width: 95%;
        overflow: hidden;
        padding:0;
        margin:0 auto;
        line-height: 110px;
        padding-top:190px;
        align-items: flex-start;
        overflow: hidden;
    }
    .hero::after{
        font-size:90px;
        align-items:flex-start;
        justify-content: flex-end;
        padding-top:280px;
        padding-left: 12px;
        overflow: hidden;
        width: 95%;
    }
    .column{
        display:flex;
        flex-direction: column;
    }
    .hero_text{
        width:85%;
        margin:0 auto;
    }
    .hero_text h1{
        font-size:2.5em;
        line-height:60px;
        margin-left:8px;
        text-align: center;
    }
    .hero_text h2{
        font-size: 1em;
        line-height: 10px;
    }
    .hero_text p{
        width:90%;
        font-size:1em;
        line-height: 25px;
        text-align: justify;
        -ms-text-justify: inter-ideograph; 
        text-justify: inter-word;
        margin:0 auto;
    }
    /*hero_img mobile*/
    .hero_img svg{
        width:60%;
    }
}