.st0{display:none;}
.st1{fill:#033359;}
.st2{fill:#E73C2B;}
.st3{opacity:0.66;fill:#E73C2B;enable-background:new    ;}
.st4{fill:none;stroke:#E73C2B;stroke-miterlimit:10;}
.st5{opacity:0.49;}
.st6{opacity:0.45;}

.ae_playbar{
    animation:aeplayer 3s infinite ease-in-out;
    fill:#E73C2B;
}
.coding_1{
    animation: codeshow 1s infinite ease-in-out;
}
.coding_2{
    animation: codeshow 2s infinite ease-in-out;
}
.coding_3{
    animation: codeshow 3s infinite ease-in-out;
}
.coding_4{
    animation: codeshow 4s infinite ease-in-out;
}
.coding_5{
    animation: codeshow 5s infinite ease-in-out;
}
.wireframe_right{
    animation: wireframeshow 5s infinite ease-in-out;
}
.wireframe_leftdown{
    animation: wireframeshow 2s infinite ease-in-out;
    fill:#E73C2B;
}
.wireframe_lefttop{
    animation: wireframeshow 3s infinite ease-in-out;
}
@keyframes aeplayer {
    0%{
       transform: translateX(-100px);
    }
    50%{
        transform: translateX(0px);
    }
    100%{
       transform: translateX(-100px);
    }
}
@keyframes codeshow{
    0%{
        opacity: 0;
        transform: translateY(0px);
    }
    50%{
        transform: translateY(-2px);
        opacity: 1;
    }
    100%{
        opacity: 0;
        transform: translateY(0px);
    }
}
@keyframes wireframeshow{
    0%{
        transform: translateY(5px);
        opacity: 0;
    }
    50%{
        transform: translateY(0px);
        opacity: 1;
    }
    100%{
        transform: translateY(5px);
        opacity: 0;
    }
}