hr{
    width:10%;
    margin:0 auto;
    padding:0;
}
.thumbnail_section{
    width:85%;
    margin:0 auto;
    height:auto;

}
.thumbnail_section::after{
    content:"WORKS";
    position: absolute;
    right:0;
    top:1500px;
    font-size:19em;
    font-weight: 900;
    font-family: 'Noto Sans TC', sans-serif;
    letter-spacing: 3px;
    color:#BDC0BA;
    opacity: 0.3;
    z-index: -10;
}
.title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}
.title h1{
    font-size: 3em;
    text-shadow: 1px 1px 3px #BDC0BA;
    margin-top: 50px;
    font-weight: 800;
}

.filter{
    display: block;
    margin:0 auto;
}
.filter button{
    margin:10px;
    height: 55px;
    width:55px;
    text-decoration: none;
    outline:none;
    border-radius: 200px;
    border:none;
    color:#F7F7F7;
    font-weight: 700;
    opacity: 0.8;
    background-color:#9c968a;
    transition:.3s ease-in-out;
    cursor: pointer;
}
.filter button:hover{
    background-color:#e73c2b;
    color:#F7F7F7;
    transform:rotate(30deg);
}

.thumbnail{
    flex:20%;
    max-width: 22%;
    margin:20px 10px 10px 10px;
    vertical-align: middle;
    width: 100%;
    border-radius: 5px;
    box-shadow: 1px 1px 3px #BDC0BA;
    overflow: hidden;
    padding-bottom: 10px;
    transition: all .3s ease-in-out;
    cursor: pointer;
    background-color:#FFFFFF ;
    width: 25%;
}
.thumbnail_image{
    width: 100%;
}
.thumbnail_image img{
    width: 100%;
}
.thumbnail:hover{
    transform: scale(1.01,1.01);
}
.desc{
    text-align: left;
    padding-left: 5px;
    display:flex;
    align-items: center;
}
.thumbnail h4{
    flex:2;
    font-size:1.2em;
    padding:5px 0px 0px 18px ;
    letter-spacing: 1px;
    color:#033359;
}
.thumbnail p{
    flex:2;
    font-size:1em;
    padding:0px 0px 0px 18px ;
    letter-spacing: 0.5px;
    color:#033359;
    font-weight: 500;
}

.language{
    flex:70%;
    display: flex;
    font-weight: 700;
    color:#91989F;
    padding-left: 15px;
    padding-top: 5px;
}


@media (max-width: 576px) { 
    .thumbnail_section{
        height: auto;
        width:95%;
    }
    .title h1{
        font-size: 2em;
    }
    .thumbnail{
        flex:100%;
        max-width:100%;
    }
    .filter{
        width: 100%;
        white-space: nowrap;
        overflow:scroll;
    }
    .filter button{
        display: inline-block;
        height: 55px;
        width:55px;
    }
    .thumbnail_section::after{
        display:none;
    }

}
@media (min-width: 576px) and (max-width: 767.98px){ 
    .thumbnail_section{
        height: auto;
        width:94%;
    }
    .thumbnail{
        flex:47%;
        max-width:48%;
    }
    .tools{
        flex:50%;
    }
    .language{
        flex:50%;
    }
    .thumbnail_section::after{
        display:none;
    }
 }
 @media (min-width: 768px) and (max-width: 991.98px){ 
    .thumbnail_section{
        height: auto;
        width:85%;
    }
    .thumbnail{
        height: auto;
        flex:30%;
        max-width:32%;
       
    }
    .tools{
        flex:40%;
    }
    .language{
        flex:60%;
    }
    .thumbnail_section::after{
        display:none;
    }
 }
 @media (min-width: 992px){
    .thumbnail_section{
        width:90%;
        height: auto;
    } 
    .thumbnail{
        height: auto;
        flex:30%;
        max-width:21%;
    }
    .tools{
        flex:40%;
    }
    .language{
        flex:60%;
    }
    
 }