.wp_menu_item{
    display: inline-block;
}
.menu_content{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 300px;
    margin:10px;
    border-radius: 10px;
    overflow: hidden;
    background-color:#FFFF ;
    box-shadow: 1px 1px 3px #BDC0BA;
    transition: all .3s ease-in-out;
}
.menu_text{
    padding:1em;
}
.menu_img img{
    width: 100%;
}
.menu_item_content a{
    text-decoration: none;
    color:#006284;
}
.menu_content:hover{
    transform: scale(1.01,1.01);
}