.tool_box{
    display: flex;
    width: 90%;
    border:2px solid #033359;
    border-radius: 10px;
    padding:10px;
    margin-top:20px;
}
.software{
    flex:5;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    border-radius:10px;
}
.software>div{
    display:flex;
    align-items: center;
    justify-content: center;
    width: 95px;
    height:30px;
    border-radius: 30px;
    text-align: center;
    margin:2px;
    font-weight: 700;
    color:white;
    background-color: #9c968a;
    margin-right:5px;
    margin-left:5px;
}
.software_title{
    flex:1;
    display:flex;
    justify-content: center;
    flex-direction: column;
    text-align: center ;
    font-size: 1.2em;
    font-weight: 400;
    letter-spacing: 1.2px;
    
}

@media (max-width: 1024px){ 
    .tool_box{
        width: 95%;
        margin-top:20px;
    }
    .software{
        flex:60%;
    }
    .software_title{
        flex:40%;
        font-size: 1em;
    }
    .software>div{
        font-size: 0.8em;
        width: 80px;
    }
}