.scroll_indicator{
    height:5vw;
    position: absolute;;
    bottom:100px;
    left: 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    letter-spacing: 1px;
    transform: rotate(90deg);
}
@media (max-width: 1024px){ 
    .scroll_indicator{
        display:none;
    }
}