.socialmeida{
    display:flex;
    flex-direction: column;
    position:fixed;
    right:0;
    bottom:150px;
    width: 75px;
    justify-content: flex-end;
    cursor: pointer;
    z-index: 2;
}
.socialmeida a{
    text-decoration: none;
    color:#032C5A;
}
.socialmeida>*{
    margin-bottom:20px;
}
.socialmeida>*:hover{
    margin-bottom:20px;
    color:#e73c2b;
}
@media (max-width:576px){
    .socialmeida{
        display: none;
    }
}