.nav_web{
    width: 90%;
    display:flex;
    z-index: 500;
    margin:0 auto;
    padding-top:50px;
    position: sticky;
    left: 0;
    top:0;
}

.brand_web{
    flex:70%;
    display:flex;
    justify-content: left;
    align-items: center;
    padding:10px;
    text-shadow: 1px 1px 3px #BDC0BA;
}

.brand_web img{
    width: 40px;
}

.menu{
    flex:33%;
    display:flex;
    justify-content: flex-end;
    align-items: center;
    list-style-type: none;
}

.menu a{
    text-decoration: none;
}

.nav_link{
    text-decoration: none;
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 1.1em;
    color:#052f57;
    padding:1em;
    transition:all .3s ease-in-out;
}

.nav_link:hover,.social_media a:hover{
    color:#e73c2b;
    text-decoration: underline;
    transform:scale(1.1,1.1);
}


/*Small devices (landscape phones, less than 768px)*/
@media (max-width: 576px) { 
    .nav_web{
        width: 100%;
        position:fixed;
        bottom:0;
        height:55px;
        top:auto;
        height: 110px;
    }
    .brand_web{
        display:none;
    }
    .nav_link{
        font-size: 1em;
        padding:1em;
    }
    .menu{
        width: 100%;
        display: flex;
        justify-content: space-around;
        background-color: #FFFFFF;
        box-shadow: 1px 1px 5px #BDC0BA;

    }
    
}
