.about_section{
    width:90%;
    display:flex;
    flex-direction:column;
    margin:0 auto;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.about_section::before{
    content:"ABOUT";
    position: absolute;
    left:0;
    padding-top:470px;
    font-size:19em;
    font-weight: 900;
    font-family: 'Noto Sans TC', sans-serif;
    letter-spacing: 3px;
    color:#BDC0BA;
    opacity: 0.3;
    z-index: -10;
}
.about_section h1{
    text-align: center;
    font-size: 3em;
    text-shadow: 1px 1px 3px #BDC0BA;
    margin-top: 50px;
    font-weight: 800;
}
.about_content{
    display:flex;
    width:95%;
    margin:0 auto;
    justify-content: space-around;
}
.about_img{
    flex:45%;
    display:flex;
    justify-content: center;
    align-items: center;
}
.about_img img{
    width:500px;
    height:500px;
    border-radius: 10px;
}
.about_text{
    flex:60%;
}
.about_align{
    width:100%;
}
.about_name h2{
    font-size: 5em;
    font-weight: 800;
    text-shadow: 1px 1px 3px #BDC0BA;
}
.about_desc{
    display:flex;
    flex-direction: column;
    width: 90%;
    font-size:1.2em;
    line-height: 30px;
}
.about_desc p:nth-child(1){
    font-weight: 500;
}
.about_desc p:nth-child(2){
    padding-top:10px;
}

@media (max-width:1024px){
    .about_section{
        height:auto;
        width: 95%;
        margin-bottom:30px;
    }
    .about_content{
        flex-direction: column;
    }
    .about_align{
        width:100%;
        margin:0 auto;
    }
    .about_name h2{
        font-size:1.5em;
        margin-left:8px;
        text-align: center;
    }
    .about_desc{
        flex-direction: column;
        line-height: 25px;
        width: 92%;
        margin:0 auto;
        font-size:1em;
    }
    .about_desc p:nth-child(2){
        padding-top:5px;
    }
    .about_img img{
        width: 100%;
        height: auto;
        margin-top: 10px;
        margin-bottom: 20px;
    }
    .about_section::before{
        display: none;

    }
}