.work h1{
    text-align: center;
    margin-top:10px;
    letter-spacing: 1px;
    text-shadow: 1px 1px 3px #BDC0BA;
}
.work_abbr{
    margin-top:5px;
    margin-bottom:30px;
    display:flex;
    justify-content:center;
    align-items: center;
    text-align: center;
    color:#91989F;
}
.work_abbr a{
    color:#91989F;
}
.work_abbr a:hover{
    color:#006284;
}
.work_abbr h3{
    font-weight: 600;
}
.tools>div{
    background-color: red;
}
.work_abbr>*{
    margin-right:30px;
}

.back{
    display:flex;
    align-items: center;
    font-size: 1.1em;
    font-weight: 700;
    padding:12px;
    color:#91989F;
    cursor: pointer;
    position:fixed;
    top:9em;
    background-color: #FFFF;
    box-shadow: 1px 1px 3px #BDC0BA;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
}
.back p{
    padding-left:10px;
}
.back:hover{
    color:#e73c2b;
}
.wp_menu{
    width: 100%;
    white-space: nowrap;
    overflow-x:scroll;
    overflow-y: hidden;
    margin-bottom:100px;
    margin-top:50px;
}
.wp_menu>div{
    display: inline-block;
}
.wp_menu a{
    text-decoration: none;
    color:#033359;
}
.work_content{
    width: 90%;
    margin:0 auto;
    display:flex;
    margin-top:15px;
}
.work_content iframe{
    width: 100%;
    height: 500px;
}
.work_img{
    flex:1;
}
.work_img img{
    display: block;
    width: 100%;
    height: auto;
}
.work_text{
    flex:1;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.1em;
}
.work_text h3{
    font-size: 1.75em;
    margin-top:10px;
}
.work_text>p{
    width:70%;
    line-height: 1.7em;
    text-align: justify;
    -ms-text-justify: inter-ideograph; 
    text-justify: inter-word;
    margin:0 auto;
}
.work_text p:nth-child(3){
    margin-top:15px;
}
@media (max-width: 576px) { 
    .work_abbr{
        flex-direction: column;
    }
    .work_content{
        width: 100%;
        flex-direction: column;
        margin-bottom: 50px;
    }
    .work_text>p{
        width:95%;
    }
    .work_content iframe{
        height: 300px;   
    }
    .work_text h3{
        font-size: 1.5em;
    }
}
@media (min-width: 576px) and (max-width: 767.98px){ 

    .work_content iframe{
        height: 300px;   
    }
    .work_content{
        width: 100%;
        flex-direction: column;
    }
    .work_text>p{
        width:95%;
    }
    .work_text h3{
        font-size: 1.5em;
    }
 }
 @media (min-width: 768px) and (max-width: 991.98px){ 

    .work_content iframe{
        height: 300px;   
    }
    .work_content{
        width: 100%;
        flex-direction: column;
    }
    .work_text>p{
        width:95%;
    }
}